/* Button */
.btn-outline-primary {
  color: #1253fa;
  color: var(--primary);
  border-color: #1253fa;
  border-color: var(--primary);
}
.btn-check:active + .btn-outline-primary,
.btn-check:checked + .btn-outline-primary,
.btn-outline-primary.active,
.btn-outline-primary.dropdown-toggle.show,
.btn-outline-primary:active,
.btn-outline-primary:hover {
  color: #1253fa;
  color: var(--primary);
  background-color: #0084e232 !important;
  background-color: var(--light-primary) !important;
  border-color: #0084e232 !important;
  border-color: var(--light-primary) !important;
}
.btn-check:focus + .btn-outline-primary,
.btn-outline-primary:focus {
  box-shadow: 0 0 0 0.25rem #0084e232 !important;
  box-shadow: 0 0 0 0.25rem var(--light-primary) !important;
}
.btn-outline-secondary {
  color: #6fbd44;
  color: var(--secondary);
  border-color: #6fbd44;
  border-color: var(--secondary);
}
.btn-check:active + .btn-outline-secondary,
.btn-check:checked + .btn-outline-secondary,
.btn-outline-secondary.active,
.btn-outline-secondary.dropdown-toggle.show,
.btn-outline-secondary:active,
.btn-outline-secondary:hover {
  color: #fff;
  background-color: #6fbd44 !important;
  background-color: var(--secondary) !important;
  border-color: #6fbd44 !important;
  border-color: var(--secondary) !important;
}
.btn-check:focus + .btn-outline-secondary,
.btn-outline-secondary:focus {
  box-shadow: 0 0 0 0.25rem rgb(111 189 68 / 50%) !important;
}
.btn-primary,
.btn-primary:hover,
.btn-primary.active,
.btn-primary.dropdown-toggle.show,
.btn-primary:active {
  background-color: #1253fa !important;
  background-color: var(--primary) !important;
  border-color: #1253fa !important;
  border-color: var(--primary) !important;
}
.btn-check:focus,
.btn-primary:focus {
  box-shadow: 0 0 0 0.25rem rgba(2, 122, 191, 0.49) !important;
}
.btn-secondary,
.btn-secondary:hover,
.btn-secondary.active,
