  width: 0%;
}
.page-daftar .container-item-step.active .inside {
  background-color: #6fbd44;
  background-color: var(--secondary);
  -webkit-animation: stepAnimate 0.5s forwards;
          animation: stepAnimate 0.5s forwards;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
@-webkit-keyframes stepAnimate {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
@keyframes stepAnimate {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
